                background: '#f7f7f7',
                borderRadius: '4px',
                marginBottom: '24px',
                border: 0,
            },
        }
    },
    methods: {
        onChange(index, status) {
            console.log(`${index}: ${status}`);
        },
    },
    watch: {
        activeIndex(val){
            console.log(val);
        },
        accordionActiveIndex(val){
            console.log(val);
        },
    },
}
</script>


